export default {
  mounted() {
    setTimeout(() => {
      const disabledInputs = document.querySelectorAll('.v-input--is-disabled');

      if (!disabledInputs.length) return;
      disabledInputs.forEach((node) => {
        setTimeout(() => {
          const button = node.querySelector('.v-input__icon--append button.v-icon--link');
          if (button) {
            button.classList.remove('v-icon--disabled');
            button.disabled = false;
          }
        });
      });
    }, 1000);
  },
};
