<template>
  <div
    class="certificates"
  >
    <div class="form-wrapper full-height">
      <v-form
        class="d-flex flex-column full-height"
        :class="{ 'justify-space-between': $vuetify.breakpoint.mobile }"
        :disabled="isFormReadOnly"
      >
        <div>
          <base-field-file
            v-model="form.values.usdot_file"
            label="USDOT certificate "
            :is-required="true"
            :rules="form.usdotFileRules"
            :disabled="isFormReadOnly"
            :append-icon="carrierCertificates.usdot_file ? 'mdi-open-in-new' : ''"
            :error="Boolean(serverError)"
            :error-messages="serverError"
            @click:append="() => openFileInNewTab(carrierCertificates.usdot_file)"
            @change="(file) => onFileChange(file, 'usdot_file')"
          />

          <base-field-file
            v-model="form.values.w9_file"
            label="W9 "
            :is-required="true"
            :rules="form.w9FileRules"
            :disabled="isFormReadOnly"
            :append-icon="carrierCertificates.w9_file ? 'mdi-open-in-new' : ''"
            :error="Boolean(serverError)"
            :error-messages="serverError"
            @click:append="() => openFileInNewTab(carrierCertificates.w9_file)"
            @change="(file) => onFileChange(file, 'w9_file')"
          />
        </div>

        <v-btn
          v-if="!isFormReadOnly"
          :width="$vuetify.breakpoint.mobile ? '100%' : 100"
          :height="$vuetify.breakpoint.mobile ? 50 : 35"
          depressed
          color="primary"
          :class="{ 'font-weight-bold mobile-fixed-buttons mobile-fullsize-button': $vuetify.breakpoint.xsOnly }"
          class="mt-2"
          @click="onSubmitCarrierCertificates"
        >
          Save
        </v-btn>
      </v-form>
    </div>
  </div>
</template>

<script>
import BaseFieldFile from '@components/BaseFieldFile';
import getFileFromUrl from '@utils/helpers/getFileFromUrl';
import getFileNameFromUrl from '@utils/helpers/getFileNameFromUrl';
import enableClickOnButtonsInsideDisabledInput from '@/mixins/disabled-input-button-clickable';
import { required } from '@utils/helpers/validationRules';
import { createNamespacedHelpers } from 'vuex';
import * as constants from '@modules/profile/constants';

const { mapState, mapActions } = createNamespacedHelpers('profile');

export default {
  name: 'ProfileCertificates',
  components: {
    BaseFieldFile,
  },
  mixins: [enableClickOnButtonsInsideDisabledInput],
  props: {
    isFormReadOnly: {
      type: Boolean,
      required: true,
    },
  },
  watch: {
    'form.values': {
      deep: true,
      handler() {
        this.serverError = '';
        Object.keys(this.serverErrors).forEach(
          (key) => {
            this.serverErrors[key] = '';
          },
        );
      },
    },
  },
  data: () => ({
    form: {
      values: {
        w9_file: null,
        usdot_file: null,
      },
      usdotFileRules: [required('USDOT File')],
      usdotValueRules: [required('USDOT Value')],
      w9FileRules: [required('W9')],
    },
    serverErrors: {},
    serverError: '',
  }),
  computed: {
    ...mapState({
      carrierCertificates: (state) => state.carrierCertificates.data,
    }),
    w9FileName() {
      return getFileNameFromUrl(this.carrierCertificates.w9_file);
    },
    usdotFileName() {
      return getFileNameFromUrl(this.carrierCertificates.usdot_file);
    },
  },
  async beforeMount() {
    const certificates = JSON.parse(JSON.stringify(this.carrierCertificates));

    this.form.values = {
      usdot_file: await getFileFromUrl(certificates.usdot_file, this.usdotFileName),
      w9_file: await getFileFromUrl(certificates.w9_file, this.w9FileName),
    };
  },
  methods: {
    openFileInNewTab(link) {
      window.open(link, '_blank');
    },
    onFileChange(file, field) {
      this.form.values[field] = file;
    },
    ...mapActions({
      editCarrierCertificates: constants.actions.EDIT_CARRIER_CERTIFICATES,
    }),
    onSubmitCarrierCertificates() {
      const profileCertificatesFormData = { ...this.form.values };

      this.editCarrierCertificates({ data: profileCertificatesFormData })
        .then(async (res) => {
          const certificates = JSON.parse(JSON.stringify(res.data.carrierCertificates));
          this.form.values = {
            usdot_file: await getFileFromUrl(certificates.usdot_file, res.data.usdotFileName),
            w9_file: await getFileFromUrl(certificates.w9_file, res.data.w9FileName),
          };
          this.$notificationSuccess('Your profile was successfully updated');
          window.location.reload();
        }).catch(() => {
          this.$notificationError('Your profile wasn\'t updated');
        });
    },
  },
};
</script>
